import React from "react";
import { Link } from "react-router-dom";

import { selectFilter } from "../../redux/filter/selectors";
import { useAppDispatch, useAppSelector } from "../../hooks";
import { selectProducts } from "../../redux/products/selectors";
import { fetchProducts } from "../../redux/products/asyncActions";
import { DataBaseError } from "../../components/DataBaseError";

import {
  Sort,
  Search,
  Categories,
  Pagination,
  ProductCard,
  SectionTitle,
  Preloader,
} from "../../components";

import s from "./Home.module.scss";
import { fetchAllProducts } from "../../redux/allProducts/asyncAction";

const Home: React.FC = () => {
  const { activeCategory, activeSort, searchValue, currentPage } =
    useAppSelector(selectFilter);
  const { items, status } = useAppSelector(selectProducts);

  const dispatch = useAppDispatch();
  const FoundProducts =
    items.length > 0 ? items.filter((obj) => obj.title !== searchValue) : [];

  const NotFoundBySearchValue = (
    <h1 className={s.notFoundBySearchValue}>
      Nothing was found for your search query: <i>"{searchValue}"</i>
    </h1>
  );

  React.useEffect(() => {
    const fetchByCategory: string = `${
      activeCategory > 0 ? `category=${activeCategory}` : ""
    }`;
    const fetchBySort: string = `sortBy=${activeSort.sortProperty.replace(
      "-",
      ""
    )}`;
    const fetchBySearch: string = `search=${searchValue}`;
    const order = `${
      activeSort.sortProperty.includes("-") ? `order=desc` : `order=asc`
    }`;
    const pagination = `page=${currentPage}&limit=4`;

    dispatch(
      fetchProducts({
        fetchByCategory,
        fetchBySort,
        fetchBySearch,
        order,
        pagination,
      })
    );
  }, [activeCategory, activeSort, searchValue, currentPage]);

  React.useEffect(() => {
    if (status === "success") {
      dispatch(fetchAllProducts());
    } else if (status === "error") {
      return;
    }
  }, [status]);

  const Products =
    items.length > 0 &&
    items.map((obj) => (
      <Link key={obj.id} to={`/product/${obj.id}`}>
        <ProductCard {...obj} />
      </Link>
    ));
  const Preloaders = [...new Array(4)].map((_, index) => (
    <Preloader key={index} />
  ));

  return (
    <main className={s.main}>
      <div className={`${s.container} container`}>
        {status !== "error" ? (
          <>
            <div className={s.refinements}>
              <div className={s.filter}>
                <Categories />
                <Sort />
              </div>
              <div className={s.searchWrapp}>
                <SectionTitle title="ALL PRODUCTS:" />
                <Search searchClassName={s.search} inputClassName={s.input} />
              </div>
            </div>
            {FoundProducts.length === 0 && status !== "loading"
              ? NotFoundBySearchValue
              : null}
            <div className={s.items}>
              {status === "loading" ? Preloaders : Products}
            </div>
            <Pagination FoundProducts={FoundProducts} />
          </>
        ) : (
          <DataBaseError />
        )}
      </div>
    </main>
  );
};

export default Home;
