import React, { useContext } from "react";
import { Outlet } from "react-router-dom";
import { CSSTransition } from "react-transition-group";

import PoppupContext from "../context/PoppupContext";
import { Menu, Header, Overlay, PageLoader } from "../components";

export const AppOutlet: React.FC = () => {
  const { setIsOpenCategories, setIsOpenSearch, setIsOpenSort, isOpenMenu } =
    useContext(PoppupContext);
  const onClickWrapper = () => {
    setIsOpenSearch?.(false);
    setIsOpenSort?.(false);
    setIsOpenCategories?.(false);
  };
  const MenuElem = (
    <CSSTransition
      in={isOpenMenu}
      timeout={400}
      classNames="menu"
      unmountOnExit
    >
      <Menu />
    </CSSTransition>
  );
  const OverlayElem = (
    <CSSTransition
      in={isOpenMenu}
      timeout={300}
      classNames="overlay"
      unmountOnExit
    >
      <Overlay />
    </CSSTransition>
  );
  return (
    <div className="wrapper" onClick={onClickWrapper}>
      {OverlayElem}
      {MenuElem}
      <Header />
      <Outlet />
    </div>
  );
};
