import React from "react";

import { useAppDispatch, useAppSelector } from "../../hooks";
import { TWishListItem } from "../../redux/wishList/types";
import { removeWishListItem } from "../../redux/wishList/slice";
import { toggleShoppingBagItem } from "../../redux/shoppingBag/slice";
import { selectShoppingBag } from "../../redux/shoppingBag/selectors";

import s from "./WishItem.module.scss";

import bagIcon from "../../assets/img/bagIcon.svg";

export const WishListItem: React.FC<TWishListItem> = ({
  title,
  imageUrl,
  price,
  id,
}) => {
  const { items: bagItems } = useAppSelector(selectShoppingBag);
  const isAddedToBag = bagItems.some((obj) => obj.id === id);

  const dispatch = useAppDispatch();

  const onRemove = () => {
    if (window.confirm("Remove?")) {
      dispatch(removeWishListItem(id));
    }
  };
  const handleToggleBagItem = () => {
    dispatch(toggleShoppingBagItem({ title, imageUrl, price, id }));
  };

  return (
    <div className={s.body}>
      <div className={s.price}>€ {price}</div>
      <div className={s.info}>
        <div className={s.image}>
          <img src={imageUrl} alt="Guess La Femme" />
        </div>
        <div className={s.wrapp}>
          <h4 className={s.title}>{title}</h4>
          <div className={s.bottom}>
            <div className={s.edit}>
              <ul className={s.editList}>
                <li onClick={onRemove} className={s.editListItem}>
                  Remove
                </li>
                <li
                  onClick={handleToggleBagItem}
                  className={s.editListItem}
                >
                  {isAddedToBag ? "Added To Bag" : "Add To Bag"}
                  <img src={bagIcon} width={20} height={18} alt="heart" />
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
