import React from "react";

import s from "./ProductsReceiptError.module.scss";

export const DataBaseError: React.FC = () => {
  return (
    <div className={s.body}>
      <h1 className={s.title}>DATA BASE ERROR</h1>
      <p className={s.message}>
        Failed to get products from database, please try again later
      </p>
    </div>
  );
};
