import React from "react";
import ReactPaginate from "react-paginate";

import { setCurrentPage } from "../../redux/filter/slice";
import { selectAllProducts } from "../../redux/allProducts/selectors";
import { useAppDispatch, useAppSelector } from "../../hooks";
import { TProducts } from "../../redux/products/types";

import s from "./Pagination.module.scss";

import arrowLeftIcon from "../../assets/img/arrowLeftIcon.svg";
import arrowRightIcon from "../../assets/img/arrowRightIcon.svg";
import { selectFilter } from "../../redux/filter/selectors";

interface IPaginationProps {
  FoundProducts: TProducts[];
}

export const Pagination: React.FC<IPaginationProps> = React.memo(
  ({ FoundProducts }) => {
    const { searchValue, activeCategory } = useAppSelector(selectFilter);
    const { items } = useAppSelector(selectAllProducts);
    const dispatch = useAppDispatch();
    const next = <img src={arrowRightIcon} alt="next" />;
    const previous = <img src={arrowLeftIcon} alt="previous" />;

    const filteredObjects = items.filter(
      (obj) => obj.category === activeCategory
    );
    const pageCount =
      filteredObjects.length !== 0
        ? Math.ceil(filteredObjects.length / 4)
        : Math.ceil(items.length / 4);

    return (
      <ReactPaginate
        className={s.body}
        breakLabel="..."
        nextLabel={next}
        previousLabel={previous}
        previousClassName={`${s.previous} ${s.toggle}`}
        nextClassName={`${s.previous} ${s.toggle}`}
        pageClassName={s.item}
        onPageChange={(event) => dispatch(setCurrentPage(event.selected + 1))}
        pageRangeDisplayed={4}
        pageCount={
          searchValue === "" ? pageCount : Math.ceil(FoundProducts.length / 4)
        }
        renderOnZeroPageCount={null}
        activeClassName={s.active}
      />
    );
  }
);
