import { createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

import { TFullProduct } from "./types";

export const fetchFullProduct = createAsyncThunk<
  TFullProduct,
  Record<string, string>
>("products/fetchProducts", async ({ id }) => {
  const URL: string = `https://63aca423da81ba976189a2ec.mockapi.io/bags/${id}`;
  const { data } = await axios.get<TFullProduct>(URL);
  return data;
});
