import React, { useContext } from "react";
import { CSSTransition } from "react-transition-group";
import useWhyDidYouUpdate from "ahooks/lib/useWhyDidYouUpdate";

import { selectFilter } from "../../redux/filter/selectors";
import { setActiveCategory } from "../../redux/filter/slice";
import { useAppDispatch, useAppSelector } from "../../hooks";
import PoppupContext from "../../context/PoppupContext";

import s from "./Categories.module.scss";

import crossIcon from "../../assets/img/crossIcon.svg";
import filterIcon from "../../assets/img/filterIcon.svg";

const categoriesList: string[] = ["all", "men", "women", "kids"];

export const Categories: React.FC = () => {
  const { activeCategory } = useAppSelector(selectFilter);

  const dispatch = useAppDispatch();
  const { isOpenCategories, setIsOpenCategories } = useContext(PoppupContext);

  const hadleCategories = (e: React.MouseEvent<HTMLButtonElement>) => {
    setIsOpenCategories?.(!isOpenCategories);
    e.stopPropagation();
  };
  const onClickCategory = (i: number) => {
    setIsOpenCategories?.(false);
    dispatch(setActiveCategory(i));
  };

  return (
    <div className={s.categories}>
      <button onClick={hadleCategories} className={s.button}>
        <img
          src={isOpenCategories ? crossIcon : filterIcon}
          width={18}
          height={18}
          alt="filter"
        />
        {isOpenCategories ? "Close Filters" : "Show Filters"}
      </button>
      <CSSTransition
        timeout={300}
        in={isOpenCategories}
        classNames="categories"
        unmountOnExit
      >
        <div className={s.poppup}>
          <ul className={s.list}>
            {categoriesList.map((category, i) => (
              <li
                key={category}
                onClick={() => onClickCategory(i)}
                className={`${s.item} ${activeCategory === i ? s.active : ""} `}
              >
                {category}
              </li>
            ))}
          </ul>
        </div>
      </CSSTransition>
    </div>
  );
};
