import { createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

import type { TProducts } from "./types";

export const fetchAllProducts = createAsyncThunk<TProducts[]>(
  "products/fetchAllProducts",
  async () => {
    const URL: string = `https://63aca423da81ba976189a2ec.mockapi.io/bags`;
    const { data } = await axios.get<TProducts[]>(URL);
    return data;
  }
);
