import React from "react";

import s from "./PageLoader.module.scss";

export const PageLoader: React.FC = () => {
  return (
    <div className={s.loading}>
      <div className={s.spinner}>
        <div className={s.mask}>
          <div className={s.maskedCircle}></div>
        </div>
      </div>
    </div>
  );
};
