import React, { useContext } from "react";
import { Link } from "react-router-dom";

import PoppupContext from "../../context/PoppupContext";

import { Logo } from "../../components";

import s from "./Menu.module.scss";

import cross from "../../assets/img/crossMenuIcon.svg";
import arrow from "../../assets/img/menuArrowIcon.svg";

type TNavList = {
  title: string;
  link: string;
};
const navList: TNavList[] = [
  {
    title: "SHOPPING BAG",
    link: "/shopping-bag",
  },
  {
    title: "WISH LIST",
    link: "/wish-list",
  },
  {
    title: "CABINET",
    link: "/cabinet",
  },
  {
    title: "CONTACT US",
    link: "/contact-us",
  },
  {
    title: "ABOUT US",
    link: "/about-us",
  },
];

export const Menu: React.FC = () => {
  const { setIsOpenMenu } = useContext(PoppupContext);
  const closeMenu = () => {
    setIsOpenMenu?.(false);
    document.body.style.overflow = "visible";
  };

  return (
    <div onClick={(e) => e.stopPropagation()} className={s.body}>
      <Logo className={s.logo} />
      <button onClick={closeMenu} className={s.closeMenuButton}>
        <img src={cross} alt="cross" />
      </button>
      <nav className={s.nav}>
        <ul className={s.list}>
          {navList.map((item) => (
            <li key={item.title} onClick={closeMenu} className={s.item}>
              <Link to={item.link} className={s.link}>
                {item.title}{" "}
                <img width={10} height={15} src={arrow} alt="arrow" />
              </Link>
            </li>
          ))}
        </ul>
      </nav>
    </div>
  );
};
