import React, { useContext } from "react";
import { Link } from "react-router-dom";

import s from "./Logo.module.scss";
import PoppupContext from "../../context/PoppupContext";

interface ILogo {
  className?: any;
}

export const Logo: React.FC<ILogo> = React.memo(({ className }) => {
  const { setIsOpenMenu } = useContext(PoppupContext);

  const closeMenu = () => {
    setIsOpenMenu?.(false);
    document.body.style.overflow = "visible";
  };

  return (
    <Link to="/" onClick={closeMenu} className={`${s.link} ${className}`}>
      AMORCE
    </Link>
  );
});
