import React, { useRef, useContext } from "react";
import { CSSTransition } from "react-transition-group";

import PoppupContext from "../../context/PoppupContext";
import { selectFilter } from "../../redux/filter/selectors";
import { setActiveSort } from "../../redux/filter/slice";
import type { TSortObject } from "../../redux/filter/types";
import { useAppDispatch, useAppSelector } from "../../hooks";

import s from "./Sort.module.scss";

import arrow from "../../assets/img/sortArrow.svg";

const sortList: TSortObject[] = [
  {
    name: "rating (desc.)",
    sortProperty: "-rating",
  },
  {
    name: "rating (asc.)",
    sortProperty: "rating",
  },
  {
    name: "price (desc.)",
    sortProperty: "-price",
  },
  {
    name: "price (asc.)",
    sortProperty: "price",
  },
];

export const Sort: React.FC = () => {
  const { activeSort } = useAppSelector(selectFilter);
  const dispatch = useAppDispatch();

  const { isOpenSort, setIsOpenSort } = useContext(PoppupContext);
  const sortRef = useRef<HTMLDivElement>(null);

  const handleOpenSort = (e: React.MouseEvent<HTMLButtonElement>) => {
    setIsOpenSort?.(!isOpenSort);
    e.stopPropagation();
  };

  const onClickSortItem = React.useCallback((obj: TSortObject) => {
    setIsOpenSort?.(false);
    dispatch(setActiveSort(obj));
  }, []);

  return (
    <div ref={sortRef} className={s.body}>
      <button onClick={handleOpenSort} className={s.button}>
        Sort By:
        <img
          className={`${s.arrow} ${isOpenSort ? s.active : ""} `}
          width={12}
          height={8}
          src={arrow}
          alt="arrow"
        />
      </button>
      <CSSTransition
        timeout={300}
        in={isOpenSort}
        classNames="sort"
        unmountOnExit
      >
        <div className={s.poppup}>
          <ul className={s.list}>
            {sortList.map((obj) => (
              <li
                key={obj.name}
                onClick={() => onClickSortItem(obj)}
                className={`${s.item} ${
                  activeSort.sortProperty === obj.sortProperty ? s.active : ""
                }  `}
              >
                {obj.name}
              </li>
            ))}
          </ul>
        </div>
      </CSSTransition>
    </div>
  );
};
