import { createSlice } from "@reduxjs/toolkit";

import type { IShoppingBagSliceState, TShoppingBagItem } from "./types";

const initialState: IShoppingBagSliceState = {
  items: [],
  totalPrice: 0,
};

const calculateTotalPrice = (items: TShoppingBagItem[]) => {
  return items.reduce((total, item) => total + item.price, 0);
};

const shoppingBagSlice = createSlice({
  name: "shoppingBag",
  initialState,
  reducers: {
    toggleShoppingBagItem(state, action) {
      const findObj = state.items.find((obj) => obj.id === action.payload.id);
      if (findObj) {
        const index = state.items.findIndex(
          (obj) => obj.id === action.payload.id
        );
        if (index !== -1) {
          state.items.splice(index, 1);
        }
      } else {
        state.items.push(action.payload);
      }
      state.totalPrice = calculateTotalPrice(state.items);
    },
    removeShoppingBagItem(state, action) {
      state.items = state.items.filter((obj) => obj.id !== action.payload);
      state.totalPrice = calculateTotalPrice(state.items);
    },
    clearShoppingBag(state) {
      state.items = [];
      state.totalPrice = 0;
    },
  },
});

export const {
  toggleShoppingBagItem,
  clearShoppingBag,
  removeShoppingBagItem,
} = shoppingBagSlice.actions;

export default shoppingBagSlice.reducer;
