import { createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

import { TProducts } from "./types";

export const fetchProducts = createAsyncThunk<
  TProducts[],
  Record<string, string>
>("products/fetchProducts", async (params) => {
  const { fetchByCategory, fetchBySort, order, pagination, fetchBySearch } =
    params;
  const URL: string = `https://63aca423da81ba976189a2ec.mockapi.io/bags?${fetchByCategory}&${fetchBySort}&${order}&${pagination}&${fetchBySearch}`;
  const { data } = await axios.get<TProducts[]>(URL);
  return data;
});
