import React from "react";

import { useAppDispatch, useAppSelector } from "../../hooks";
import { removeShoppingBagItem } from "../../redux/shoppingBag/slice";
import { toggleWishListItem } from "../../redux/wishList/slice";
import { selectShoppingBag } from "../../redux/shoppingBag/selectors";
import { selectWishList } from "../../redux/wishList/selectors";

import s from "./ShoppingBagItem.module.scss";

interface IShoppingBagItem {
  id: string;
  title: string;
  price: number;
  imageUrl: string;
}

export const ShoppingBagItem: React.FC<IShoppingBagItem> = ({
  title,
  price,
  imageUrl,
  id,
}) => {
  const { items } = useAppSelector(selectWishList);
  const isAddedToWishList = items.some((obj) => obj.id === id);
  const {} = useAppSelector(selectShoppingBag);
  const dispatch = useAppDispatch();

  const onRemove = () => {
    dispatch(removeShoppingBagItem(id));
  };
  const handleToggleWishListItem = () => {
    dispatch(toggleWishListItem({ title, price, imageUrl, id }));
  };

  return (
    <div className={s.body}>
      <div className={s.price}>€ {price}</div>
      <div className={s.info}>
        <div className={s.image}>
          <img src={imageUrl} alt="Guess La Femme" />
        </div>
        <div className={s.wrapp}>
          <h4 className={s.title}>{title}</h4>
          <div className={s.bottom}>
            <div className={s.edit}>
              <ul className={s.editList}>
                <li onClick={onRemove} className={s.editListItem}>
                  Remove
                </li>
                <li
                  onClick={handleToggleWishListItem}
                  className={s.editListItem}
                >
                  {isAddedToWishList
                    ? "Added To Wish List"
                    : "Add To Wish List"}
                  <svg
                    width="20"
                    height="18"
                    viewBox="0 0 20 18"
                    fill={isAddedToWishList ? "#000" : "none"}
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M10 16.84C10 16.84 1 11.8 1 5.68001C1 4.59811 1.37484 3.54962 2.06076 2.71294C2.74667 1.87626 3.70128 1.30307 4.76218 1.09089C5.82307 0.878711 6.92471 1.04065 7.87966 1.54916C8.83461 2.05767 9.58388 2.88133 10 3.88001C10.4161 2.88133 11.1654 2.05767 12.1203 1.54916C13.0753 1.04065 14.1769 0.878711 15.2378 1.09089C16.2987 1.30307 17.2533 1.87626 17.9392 2.71294C18.6252 3.54962 19 4.59811 19 5.68001C19 11.8 10 16.84 10 16.84Z"
                      stroke="black"
                      strokeOpacity="0.75"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </svg>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
