import { createSlice } from "@reduxjs/toolkit";

import { fetchFullProduct } from "./asyncActions";
import { IFullProductSliceState, TFullProduct } from "./types";

const initialState: IFullProductSliceState = {
  fullProductItem: {} as TFullProduct,
  status: "loading",
};

const fullProductSlice = createSlice({
  name: "products",
  initialState,
  reducers: {
    setFullProduct(state, action) {
      state.fullProductItem = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(fetchFullProduct.pending, (state) => {
      state.status = "loading";
      state.fullProductItem = {} as TFullProduct;
    });
    builder.addCase(fetchFullProduct.fulfilled, (state, action) => {
      state.status = "success";
      state.fullProductItem = action.payload;
    });
    builder.addCase(fetchFullProduct.rejected, (state) => {
      state.status = "error";
      state.fullProductItem = {} as TFullProduct;
    });
  },
});

export const { setFullProduct } = fullProductSlice.actions;

export default fullProductSlice.reducer;
