import { createSlice } from "@reduxjs/toolkit";

import type { IWishListSliceState } from "./types";

const initialState: IWishListSliceState = {
  items: [],
};

const wishListSlice = createSlice({
  name: "wishList",
  initialState,
  reducers: {
    toggleWishListItem(state, action) {
      const findObj = state.items.find((obj) => obj.id === action.payload.id);
      if (findObj) {
        const index = state.items.findIndex(
          (obj) => obj.id === action.payload.id
        );
        if (index !== -1) {
          state.items.splice(index, 1);
        }
      } else {
        state.items.push(action.payload);
      }
    },
    removeWishListItem(state, action) {
      state.items = state.items.filter((obj) => obj.id !== action.payload);
    },
    clearWishList(state) {
      state.items = [];
    },
  },
});

export const { toggleWishListItem, clearWishList, removeWishListItem } = wishListSlice.actions;

export default wishListSlice.reducer;
