import React, { Suspense } from "react";
import { Route, Routes } from "react-router-dom";

import PoppupContext from "./context/PoppupContext";

import { PageLoader } from "./components";
import { AppOutlet } from "./outlets/AppOutlet";

import Home from "./pages/Home";

import "./assets/scss/app.scss";

const WishList = React.lazy(
  () => import(/* webpackChunkName: 'WishList' */ "./pages/WishList")
);
const ShoppingBag = React.lazy(
  () => import(/* webpackChunkName: 'ShoppingBag' */ "./pages/ShoppingBag")
);
const FullProduct = React.lazy(
  () => import(/* webpackChunkName: 'FullProduct' */ "./pages/FullProduct")
);
const NotFound = React.lazy(
  () => import(/* webpackChunkName: 'NotFound' */ "./pages/NotFound")
);

export const App: React.FC = () => {
  const [isOpenSort, setIsOpenSort] = React.useState<boolean>(false);
  const [isOpenMenu, setIsOpenMenu] = React.useState<boolean>(false);
  const [isOpenSearch, setIsOpenSearch] = React.useState<boolean>(false);
  const [isOpenCategories, setIsOpenCategories] =
    React.useState<boolean>(false);

  const PoppupContextValue = {
    isOpenMenu,
    setIsOpenMenu,
    isOpenSearch,
    setIsOpenSearch,
    isOpenSort,
    setIsOpenSort,
    isOpenCategories,
    setIsOpenCategories,
  };

  return (
    <PoppupContext.Provider value={PoppupContextValue}>
      <Routes>
        <Route path="/" element={<AppOutlet />}>
          <Route path="/" element={<Home />} />
          <Route
            path="/shopping-bag"
            element={
              <Suspense fallback={<PageLoader />}>
                <ShoppingBag />
              </Suspense>
            }
          />
          <Route
            path="/wish-list"
            element={
              <Suspense fallback={<PageLoader />}>
                <WishList />
              </Suspense>
            }
          />
          <Route
            path="/product/:id"
            element={
              <Suspense fallback={<PageLoader />}>
                <FullProduct />
              </Suspense>
            }
          />
          <Route
            path="*"
            element={
              <Suspense fallback={<PageLoader />}>
                <NotFound />
              </Suspense>
            }
          />
        </Route>
      </Routes>
    </PoppupContext.Provider>
  );
};

export default App;
