import React, { useContext } from "react";
import { Link, useLocation } from "react-router-dom";
import { CSSTransition } from "react-transition-group";

import { setSearchvalue } from "../../redux/filter/slice";
import { useAppDispatch } from "../../hooks";

import PoppupContext from "../../context/PoppupContext";

import { Logo, Search } from "../../components";

import s from "./Header.module.scss";

import bagIcon from "../../assets/img/bagIcon.svg";
import crossIcon from "../../assets/img/crossIcon.svg";
import menuIcon from "../../assets/img/menuIcon.svg";
import heartIcon from "../../assets/img/heartIcon.svg";
import searchIcon from "../../assets/img/searchIcon.svg";

export const Header: React.FC = () => {
  const dispatch = useAppDispatch();
  const { setIsOpenMenu } = useContext(PoppupContext);
  const { isOpenSearch, setIsOpenSearch } = useContext(PoppupContext);

  const handleOpenSearch = (e: React.MouseEvent<HTMLButtonElement>) => {
    setIsOpenSearch?.(!isOpenSearch);
    setTimeout(() => dispatch(setSearchvalue("")), 300);
    e.stopPropagation();
  };

  const openMenu = React.useCallback(() => {
    setIsOpenMenu?.(true);
    document.body.style.overflow = "hidden";
  }, []);

  const { pathname } = useLocation();

  return (
    <header className={s.body}>
      <div className={`container ${s.container}`}>
        <div className={s.leftWrapp}>
          <button onClick={openMenu} className={`${s.buttonMenu}`}>
            <img src={menuIcon} alt="menu" />
          </button>
          <Logo />
        </div>
        <div className={s.rightWrapp}>
          <CSSTransition
            timeout={300}
            classNames="search"
            in={isOpenSearch}
            unmountOnExit
          >
            <Search searchClassName={s.search} inputClassName={s.input} />
          </CSSTransition>
          {pathname === "/" && (
            <button onClick={handleOpenSearch} className={s.searchButton}>
              <img
                src={isOpenSearch ? crossIcon : searchIcon}
                width={18}
                height={18}
                alt={isOpenSearch ? "cross" : "search"}
              />
            </button>
          )}
          {pathname !== "/shopping-bag" && (
            <Link to="/shopping-bag" className={`${s.button}`}>
              <img src={bagIcon} alt="bag" />
            </Link>
          )}
          {pathname !== "/wish-list" && (
            <Link to="/wish-list" className={`${s.button}`}>
              <img src={heartIcon} alt="heart" />
            </Link>
          )}
        </div>
      </div>
    </header>
  );
};
