import { createContext, Dispatch, SetStateAction } from "react";

type TSetState<T> = Dispatch<SetStateAction<T>>;

interface IContext {
  isOpenMenu: boolean;
  setIsOpenMenu?: TSetState<boolean>;
  isOpenSearch: boolean;
  setIsOpenSearch?: TSetState<boolean>;
  isOpenSort: boolean;
  setIsOpenSort?: TSetState<boolean>;
  isOpenCategories: boolean;
  setIsOpenCategories?: TSetState<boolean>;
}

const PoppupContext = createContext<IContext>({
  isOpenMenu: false,
  isOpenSearch: false,
  isOpenSort: false,
  isOpenCategories: false,
});

export default PoppupContext;
