import React, { useRef } from "react";
import Typed from "typed.js";

import s from "./ProductCard.module.scss";

import productImagePreloader from "../../assets/img/productImagePreloader.svg";

export const Preloader: React.FC = () => {
  React.useEffect(() => {
    const titleTyped = new Typed(titleSpanRef.current, {
      strings: ["..."],
      typeSpeed: 100,
      cursorChar: "",
      loop: true,
    });
    const priceTyped = new Typed(priceSpanRef.current, {
      strings: ["..."],
      typeSpeed: 100,
      cursorChar: "",
      loop: true,
    });
    return () => {
      titleTyped.destroy();
      priceTyped.destroy();
    };
  }, []);

  const titleSpanRef = useRef<HTMLSpanElement>(null);
  const priceSpanRef = useRef<HTMLSpanElement>(null);

  return (
    <div className={s.body}>
      <div className={`${s.image} ${s.preloaderImage}`}>
        <img
          className={s.imagePreloader}
          src={productImagePreloader}
          alt="preloader"
        />
      </div>
      <h4 className={s.title}>
        Loading <span ref={titleSpanRef}></span>
      </h4>
      <div className={s.price}>
        € <span ref={priceSpanRef}></span>
      </div>
    </div>
  );
};
